import React, { createContext, useState, useEffect } from "react";
import { recipesGet } from "../constants";

export const RecetasContext = createContext();

const RecetasContextProvider = ({ children }) => {
  const [doneFetch, setDoneFetch] = useState();
  const [recetas, setRecetas] = useState([]);
  const text = "Recetas";
  // const textLikes = "Mis me gusta";

  useEffect(() => {
    getTopRecipes();
  }, []);

  const getTopRecipes = () => {
    fetch(recipesGet())
      .then(res => res.json())
      .then(data => {
        setDoneFetch(true);
        setRecetas(data);
        console.log(data)
      })
      .catch((err) => console.log(err));
  };

  // Patrn de diseo PROVIDER aplicado en RecetasContext.Provider para disponibilizar el rbol de datos (value) a otros contextos
  return (
    <RecetasContext.Provider value={{ 
      doneFetch, 
      text, 
      // textLikes,
      recetas, 
      }}>
      {children}
    </RecetasContext.Provider>
  );
};

export default RecetasContextProvider;
