import React from "react";

const Message = ({ text, textLikes}) => (
  <h2
    style={{
      backgroundColor: "#24aa87",
      borderRadius: 15,
      padding: 10,
      marginTop: 20,
      marginBottom: 30,
      marginLeft: 50,
      color: "whitesmoke",
      textAlign: "center",
      width: "22.5%",
    }}
  >
    <>
    {text}
    {textLikes}
    </>
  </h2>
);

export default Message;
