import React, { lazy, Suspense } from "react";
import { BrowserRouter, Switch, Route } from "react-router-dom";

import Header from "../components/header/header.components";
import Footer from "../components/footer/footer.components";

import RecipesContextProvider from "../contexts/RecetasContext";
import RecipeDetailsContextProvider from "../contexts/RecipeDetailsContext";
import SavesContextProvider from "../contexts/Saves";

// import Recipes from "../components/Recipes";
import RecipeDetails from "../components/RecipeDetails";
import Favoritos from '../components/Favoritos/index';
import "../assets/css/styles.css";
const Recipes = lazy(() => import('../components/Recipes'));
const NotFound = lazy(() => import('../components/NotFound'));

class App extends React.Component {
  render() {
    return (
      <div>
        <BrowserRouter>
        <Suspense
          fallback={
            <div>
              {/* <Loader /> */}
              ...loading
            </div>
          }
        >
          
          <Header />
          <SavesContextProvider>
          <Switch>
          
            <Route exact path="/">
              {/* HOC en RecipesContextProvider para Englobar la definicin de Recipes y pasarle otras caractersticas, en este caso el context de Recipes que recibe la API */}
              <RecipesContextProvider>   
               <Recipes />
              </RecipesContextProvider>
            </Route>
            <Route path="/recetas/id/:id">
              {/* HOC en RecipeDetailsContextProvider para Englobar la definicin de RecipeDetails y pasarle otras caractersticas, en este caso el context de RecipeDetails que recibe la data de la receta segn el id */}
              <RecipeDetailsContextProvider>
                <RecipeDetails />
              </RecipeDetailsContextProvider>
            </Route>
            <Route path="/favoritos">
              {/* <SavesContextProvider> */}
                <Favoritos/> 
              {/* </SavesContextProvider> */}
              
            </Route>
            
            <Route component={NotFound}></Route>
            
          </Switch>
          </SavesContextProvider>
          <Footer></Footer>
          
          </Suspense>
          
        </BrowserRouter>
      </div>
    );
  }
}

export default App;
