import React, {useContext, useState, useEffect} from 'react';
import { ModalTitle } from 'react-bootstrap';
import { Card, CardHeader, CardBody, Row, Col, CardSubtitle, CardTitle, CardFooter, CardImg, Button,  Modal, ModalBody, ModalFooter, ModalHeader} from 'reactstrap';
import {CartContext} from '../../contexts/Saves';
import { Clock, User, Star, Heart } from "react-feather";

const AddFavoritos = ({ modalVisible, setModalVisible, receta, id, name, handleOpenModal, handleCloseModal, modal}) => {
  const context = useContext(CartContext);
  const {cart, setCart, existe, setExiste} = context;
  // const nuevoItem = 'Cart3';
//   console.log(character);

  // const [modalVisible, setModalVisible] = useState(false);

  // const [centeredModal, setCenteredModal] = useState(false);
  // useEffect(() => {
  //   const openModal = async () => {
  //     setCenteredModal(centeredModal);
  //   };
  // openModal();
  // }, [centeredModal]);

  // const openModal = () => {
  //   setModalVisible(!modalVisible);
  // };

  return (
    <Modal
            isOpen={modal}
            toggle={handleCloseModal}
            className="modal-m"
            centered={true}
            // style={{marginTop:'7%'}}
        >
            <ModalHeader toggle={modal}>
              <ModalTitle>
              <h3 style={{marginVertical: 10, color: 'grey', fontSize: 20, fontWeight: 'bold'}}>Deseas agregar esta receta a tus me gusta?</h3>
              </ModalTitle>
              <br></br>
              <h5 style={{color: 'grey'}}>
                <Heart
                    strokeWidth={3}
                    fill="red"
                    size={25}
                    color="red"
                    style={{
                      color: "red",
                      marginRight: 10,
                      // backgroundColor: "red",
                      // borderRadius: 10,
                      // borderBottomColor: "red",
                      margin: 5,
                    }}
                  />
                  {name}</h5> 
               <br></br>
            </ModalHeader>
            <ModalBody>

            <Row>
              <Col>

              <Button style={{
                backgroundColor: 'red',
                padding: 5,
                marginVertical: 5,
                borderRadius: 5,
                width: '100%',
                alignSelf: 'center',
                margin: 5,
                }}
                onClick={() => 
                  {
                  // setModalVisible(false)
                  // setCart([...cart, character]);
                  // setCart([...cart, character]);
                  setCart(cart.concat(receta));
                  console.log(cart);
                  setExiste(existe.concat(id));
                  console.log(id);
                  {handleCloseModal()}
                  
                  // setExiste([...existe, character])
                  }}>
                    <h5 style={{fontSize: 20, paddingHorizontal: 5, justifyContent: 'center', alignSelf: "center"}}>
                        S
                    </h5>
                </Button>

              </Col>

              <Col>
              <Button style={{
                backgroundColor: 'grey',
                padding: 5,
                marginVertical: 5,
                borderRadius: 5,
                width: '100%',
                alignSelf: 'center',
                margin: 5
                }}
                // onClick={
                //   () => setModalVisible(false)}
                onClick={() => handleCloseModal()}
                  >
                    <h5 style={{fontSize: 20, paddingHorizontal: 5, justifyContent: 'center', alignSelf: "center"}}>
                        No
                    </h5>
                </Button>
              </Col>
            </Row>
           
                
            </ModalBody>
 
</Modal>
  );
};

export default AddFavoritos;
