import React, {Fragment, useContext, useState} from 'react';
import {
  Card,
  CardHeader,
  CardBody,
  Row,
  Col,
  CardSubtitle,
  CardTitle,
  CardFooter,
  CardImg,
  Button
} from "reactstrap";
import { Clock, User, Bookmark } from "react-feather";
import Flags from "country-flag-icons/react/3x2";

import {CartContext} from '../../contexts/Saves';
import ModalAdd from '../../components/ModalAddFavoritos';

const tipoComida = (tipo) => {
  if (tipo === "China")
    return <Flags.CH title="China" style={{ width: "5%", borderRadius: 10 }} />;
  if (tipo === "Espaola")
    return <Flags.ES title="Espaa" style={{ width: "5%" }} />;
  if (tipo === "Chilena")
    return <Flags.CL title="Chile" style={{ width: "5%" }} />;
  if (tipo === "Italiana")
    return <Flags.IT title="Italia" style={{ width: "5%" }} />;
  if (tipo === "Egipto")
  return <Flags.EG title="Egipto" style={{ width: "5%" }} />;
  if (tipo === "Francia")
  return <Flags.FR title="Francia" style={{ width: "5%" }} />;
};

const Details = ({ detalleReceta }) => {
  const context = useContext(CartContext);
  const {cart, setCart, existe, setExiste} = context;
  const [modalVisible, setModalVisible] = useState(false);

  const {
    name,
    tiempo,
    porciones,
    tipo,
    url,
    prep,
    receta,
  } = detalleReceta;

  return (
    <div style={{ margin: 20 }}>
       <ModalAdd
        modalVisible={modalVisible}
        setModalVisible={setModalVisible}
        character={name}
      />

      <Card style={{ borderRadius: 15, marginBottom: 20 }}>
        <CardHeader
          style={{
            backgroundColor: "#a0a0a0ba",
            borderTopLeftRadius: 15,
            borderTopRightRadius: 15,
          }}
        >
          <Button onClick={() => console.log(cart)}>Print cart</Button>
          <CardTitle>
            <Row style={{ justifyContent: "space-between", margin: 10 }}>
              <h2>{name}</h2>
              {tipoComida(tipo)}
            </Row>
          </CardTitle>
          <CardSubtitle>
            <Row align="left"></Row>
          </CardSubtitle>
        </CardHeader>

        <CardBody>
          <Row>
            <Col
              style={{
                alignItems: "center",
                flexDirection: "column",
                alignSelf: "center",
              }}
            >
              <Col>
                <Clock size={20} /> Tiempo de preparacin:{" "}
                <strong>{tiempo}</strong> minutos
              </Col>

              <Col>
                <User size={20} /> Porciones: <strong>{porciones}</strong>
              </Col>
              <br />
              <CardImg
                component="img"
                height={250}
                src={url}
                alt="fotografa receta"
                style={{ borderRadius: 10, width: 500 }}
              />
            </Col>
            <Col>
              <div style={{ textAlign: "justify", marginTop: 10, height: 110 }}>
                <strong>
                  <h3>
                    {" "}
                    <Bookmark size={25} /> <i>Preparacin</i>
                  </h3>
                </strong>
                <hr />
                <i>{prep}</i>
              </div>
            </Col>
          </Row>
          <br />
          <br />
          <br />
        </CardBody>

        <CardFooter
          style={{
            borderBottomLeftRadius: 15,
            borderBottomRightRadius: 15,
          }}
        >
          <Row>
            <Col style={{ width: "25%", textAlign: "left" }} align="center">
              <div>
                <strong>
                  <h3>
                    {" "}
                    <Bookmark size={25} /> <i>Receta</i>
                  </h3>
                </strong>
                <hr />

                <Row style={{ alignItems: "flex-start" }}>
                  <Col>
                    {receta?.INGREDIENTES.length >= 1 ? (
                      <div
                        style={{
                          marginHorizontal: 20,
                          fontSize: 20,
                          marginBottom: 10,
                        }}
                      >
                        Ingredientes:
                      </div>
                    ) : null}

                    {receta?.INGREDIENTES
                      ? receta?.INGREDIENTES.map((ingrediente) => (
                          <>
                            <div
                              style={{ marginHorizontal: 20, fontSize: 15 }}
                              key={ingrediente}
                            >
                              - {ingrediente}
                            </div>
                          </>
                        ))
                      : null}
                  </Col>

                  <Col>
                    {receta?.MASA.length >= 1 ? (
                      <div
                        style={{
                          marginHorizontal: 20,
                          fontSize: 20,
                          marginBottom: 10,
                        }}
                      >
                        Masa:
                      </div>
                    ) : null}

                    {receta?.MASA
                      ? receta?.MASA.map((ingrediente) => (
                          <>
                            <div
                              style={{ marginHorizontal: 20, fontSize: 15 }}
                              key={ingrediente}
                            >
                              - {ingrediente}
                            </div>
                          </>
                        ))
                      : null}
                  </Col>

                  <Col>
                    {receta?.RELLENO.length >= 1 ? (
                      <div
                        style={{
                          marginHorizontal: 20,
                          fontSize: 20,
                          marginBottom: 10,
                        }}
                      >
                        Relleno:
                      </div>
                    ) : null}

                    {receta?.RELLENO
                      ? receta?.RELLENO.map((ingrediente) => (
                          <>
                            <div
                              style={{ marginHorizontal: 20, fontSize: 15 }}
                              key={ingrediente}
                            >
                              - {ingrediente}
                            </div>
                          </>
                        ))
                      : null}
                  </Col>

                  <Col>
                    {receta?.TOPPING.length >= 1 ? (
                      <div
                        style={{
                          marginHorizontal: 20,
                          fontSize: 20,
                          marginBottom: 10,
                        }}
                      >
                        Topping:
                      </div>
                    ) : null}

                    {receta?.TOPPING
                      ? receta?.TOPPING.map((ingrediente) => (
                          <>
                            <div
                              style={{ marginHorizontal: 20, fontSize: 15 }}
                              key={ingrediente}
                            >
                              - {ingrediente}
                            </div>
                          </>
                        ))
                      : null}
                  </Col>
                </Row>
              </div>
            </Col>
          </Row>
        </CardFooter>
      </Card>
    </div>
  );
};

export default Details;
